.search-header {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  height: 102px;
  padding: 20px 0 14px;
  margin: 0 10px;
  box-sizing: border-box;
  gap: 23px;
  border-bottom: 1px solid $lightBorderColor;
  &__link {
    color: $greyColor;
    font-weight: 500;
    font-size: 12px;
    line-height: 12px;

    text-decoration: none;
  }
}